import { h, Component } from 'preact';

export default class About extends Component {
	render() {
		return (
			<div id="about">
				<h1 className="secondary-title">Om oss</h1>
				<p>
					Individanpassad cirkeltrning fr hela familjen! Vi finns i Wirns keris lokal bredvid bilpoolen.
					All trning sker p egen risk. Vlkomna!
				</p>
			</div>
		);
	}
}


